<template>        
    <div class="row align-items-center p-2" v-if="isOwnProfile && activeDocs?.length > 0">
        <h4><i class="fa fa-pencil mr-1"></i></h4>
        <label for="editCardsCheckbox" class="el-switch"><input type="checkbox" name="switch" v-model="editMode" id="editCardsCheckbox">
          <span class="el-switch-style"></span>
        </label>  
    </div>

    
    <div class="row align-items-center mx-sm-3">
      <ContentCard v-for="{postUid, contentCard} in activeDocs" :key="postUid" :contentCard="contentCard" :editMode="editMode" :userUid="userUid" :postUid="postUid" :onDelete="() => onDelete(postUid)" />
    </div>
  
    <div class="center" v-if="pagination?.pages?.length > 1">
      <button style="width: 30px; height: 30px;" @click="updatePagination(pagination.startPage)">{{pagination.startPage}}</button>
      <button style="width: 30px; height: 30px;" v-for="page in pagination.midPages" :key="`page-${page}`" @click="updatePagination(page)">{{page}}</button>
      <button style="width: 30px; height: 30px;" @click="updatePagination(pagination.endPage)">{{pagination.endPage}}</button>
    </div>
</template>

<script lang="ts">
import { reactive, toRefs, onMounted } from 'vue'
import { auth, getTimelineCollection } from '../firebase.config'
import ContentCard from '@/components/shared/ContentCard'
import paginate from '@/utils/paginate'
import { BaseContentCard } from '@/types/BaseContentCard'

export default {
  components: {
    ContentCard,
  },
  props: {
    userUid: String,
  },
  async setup (props) {
    console.log('ProfileTimeline', { props })
    const isOwnProfile = auth?.currentUser?.uid === props.userUid

    const state = reactive({
      postRefs: [],
      activeDocs: [],
      pagination: { 
        startIndex: 0,
        endIndex: 0,
        currentPage: 1,
        startPage: 1,
        endPage: 1,
        pageSize: 20,
        midPages: [],
      },
      editMode: false
    })

    const updatePagination = (toPage = state.pagination.currentPage) => {
      state.pagination = {
        ...state.pagination, 
        ...paginate(state.postRefs.length, toPage, state.pagination.pageSize)
      }

      const current = state.pagination.currentPage
      const prev = current - 1 >= state.pagination.startPage ? current - 1 : 0;
      const next = current + 1 <= state.pagination.endPage ? current + 1 : 0;
      const midPages = [prev, current, next].filter(p => p > state.pagination.startPage && p < state.pagination.endPage)
      
      state.pagination.midPages = midPages
      state.activeDocs = state.postRefs.slice(state.pagination.startIndex, state.pagination.endIndex + 1).map(d => ({ postUid: d.id, contentCard: d.data() as BaseContentCard }))
    }  

    const timelineRef = getTimelineCollection(props.userUid)
    const loadDocs = (timelineData) => {
        state.postRefs = timelineData.docs
        updatePagination()
    }
 
    //Keep updating template for own user profile
    onMounted(() => {
      if (isOwnProfile) {
          timelineRef.orderBy('ts', 'desc').onSnapshot(loadDocs)
      }
    })

    timelineRef.orderBy('ts', 'desc').get().then(loadDocs)


    const onDelete = (postUid: string) => confirm('sure?') ? timelineRef.doc(postUid).delete() : null
       
    return {
        ...toRefs(state),
        isOwnProfile,
        updatePagination,
        onDelete
    }
  }
}
</script>
