import { profilesCollection, storageRef } from '@/firebase.config'
import { reactive, toRefs, computed, onMounted } from 'vue'
import { auth, getTimelineCollection } from '../firebase.config'
import router from '@/router'
import { UserProfile } from '@/types/UserProfile'
import EditableTextInput from '@/components/shared/EditableTextInput'
import EditableImgInput from '@/components/shared/EditableImgInput'
import CollapsibleContent from '@/components/shared/CollapsibleContent'
import Spinner from '@/components/shared/Spinner'
import ProfileTimeline from '@/components/ProfileTimeline'
import updateProfileValue from '@/services/updateProfileValue'
import { FetchUrlMeta } from '@/services/FetchUrlMeta'
import { BaseContentCard } from '@/types/BaseContentCard'
import { invertHex } from '@/utils/utils'
import { createHash } from 'crypto'
import parseKnownUrl from '@/controllers/KnownUrlsParser'

export default {
  components: {
    EditableTextInput,
    EditableImgInput,
    ProfileTimeline,
    CollapsibleContent,
    Spinner
  },
  props: {
    userUid: String,
  },
  async setup (props) {
    const isOwnProfile = auth?.currentUser?.uid === props.userUid

    const shareLinks = {
      regular: `/#/u/${props.userUid}`,
      insta: `/user?u=${props.userUid}`,
    }
  
    onMounted(() => {
      /* const parent = document.getElementById('color-picker')
      parent.innerHTML = ''
      // eslint-disable-next-line no-undef
      const picker = new Picker({ parent, alpha: false, popup: false, color: reactiveFields.updateColor || reactiveFields.profileData.bgColor }) // https://vanilla-picker.js.org/gen/Picker.html#Picker__anchor

      // picker.onOpen = (test) => { document.querySelector('.picker_alpha').style.display = 'none'; }

      picker.onChange = ({hex: hexChange}) => {
        // console.log({ hexChange })
        // eslint-disable-next-line @typescript-eslint/no-use-before-define
        reactiveFields.updateColor = hexChange.slice(0,7)
      } 
      picker.onDone = ({hex: hexDone}) => {
        // console.log({ hexDone })
        updateProfileValue('bgColor', hexDone.slice(0,7))
        reactiveFields.editColorMode = false
      }
      picker.onClose = () => {
        // console.log({ hexClosed })
        reactiveFields.updateColor = ''
      }
      picker.onOpen = ({hex: hexOpened}) => {
        console.log({ hexOpened })
        // eslint-disable-next-line @typescript-eslint/no-use-before-define
        reactiveFields.updateColor = hexOpened.slice(0,7)
      }

      // eslint-disable-next-line no-undef
      const newQR = new VanillaQR({
        url: shareLinks.regular,
        // colorLight: invertedHex.value || 'white',
        // colorDark: reactiveFields?.updateColor || reactiveFields?.profileData?.bgColor || 'black',
        noBorder: true
      });
      const qrBox = document.getElementById('qr-box')
      if (!qrBox) return
      qrBox.innerHTML = ''  
      qrBox.appendChild(newQR.domElement)

       */
      //Keep updating user profile template for own user profile
      if (isOwnProfile) {
        userRef.onSnapshot((userDoc) => {
          console.log({ ...userDoc.data() })
          reactiveFields.profileData = userDoc.data() as UserProfile
        })
      }
    })

    
    const socialLinksToShow = computed(() => {
      const filled = {}
      Object.keys(reactiveFields.profileData.socialLinks).filter(key => reactiveFields?.profileData?.socialLinks[key]?.link?.length > 0).forEach(key => {
        const { link = '', icon } = reactiveFields?.profileData?.socialLinks[key]
        const enabled = link?.length > 0
        console.log({ icon, link, enabled })
        filled[key] = {
          link,
          icon,
          enabled,
        }
      })
      return filled
    })

    const userRef = profilesCollection.doc(props.userUid)
    const userDoc = await userRef.get()
    if (!userDoc?.exists) {
      console.log('Profile not found, TODO 404')
      router.replace('/404')
      return
    }



    
    // eslint-disable-next-line @typescript-eslint/no-use-before-define
    const invertedHex = computed(() => invertHex(reactiveFields.updateColor || reactiveFields.profileData.bgColor))
    
    const reactiveFields = reactive({
      profileData: userDoc.data() as UserProfile, 
      editMode: false,
      editSocialLinkMode: false,
      editColorMode: false,
      socialLinkInputFor: '',
      trySocialLink: '',
      tryURL: '',
      tryFile: null,
      isUploading: false,
      updateColor: '',
      /* showShareProfile: false, */
    })
    
    const isLoggedIn = auth?.currentUser?.uid?.length > 1 || false
    const signOut = async () => {
        await auth.signOut()
        // router.replace('/login')
    }

    const tryContentCardUpload = async (contentCard?: BaseContentCard): Promise<void> => {
      reactiveFields.isUploading = true
      let newContentCard: BaseContentCard
      try {
        if (contentCard) {
          newContentCard = contentCard
        } else {
          newContentCard = await parseKnownUrl(reactiveFields.tryURL.trim())
          if (newContentCard?.type) {
            newContentCard = {
              ...newContentCard,
              ...(await FetchUrlMeta(newContentCard.sourceUrl, newContentCard.type))
            }
          } else {
            newContentCard = {
              ...newContentCard,
              ...(await FetchUrlMeta(reactiveFields.tryURL.trim()))
            }
          }
        }

        const { currentUser } = auth
        const { uid } = currentUser || {}
        if (!uid) return
        const docRef = getTimelineCollection(uid)
        const { id: newPostUid } = await docRef.add(newContentCard)
        reactiveFields.tryURL = ''
        console.log('New link posted! 👍', { newPostUid })
        router.replace(`/u/${uid}/p/${newPostUid}`)

      } catch ({ code, message }) {
        console.error('addContent Error', { code, message })
      }
      reactiveFields.isUploading = false
    }

    const handleImageFile = async ({ target }) => {
      reactiveFields.isUploading = true
      try {
        const { files: [file,] } = target
        console.log({ file: File })
        const fileNameHash = createHash('sha1').update(file.name).digest('base64')
        const fileRef = storageRef.child(`${auth?.currentUser?.uid}/${new Date().getTime()}_${fileNameHash}`)
        const { ref } = await fileRef.put(file)
        const downloadURL = await ref.getDownloadURL()
        console.log({ downloadURL })
        target.value = null
        await tryContentCardUpload({
          type: 'image',
          sourceUrl: downloadURL,
          previewImage: downloadURL,
          rawImages: [downloadURL],
          ts: new Date(),
          title: '',
          description: ''
        })
       } catch ({ code, message }) {
        console.error('handleImageFile Error', { code, message })
      }
      reactiveFields.isUploading = false
    }

    const handleVideoFile = async ({ target }) => {
      reactiveFields.isUploading = true
      try {
        const { files: [file,] } = target
        console.log({ file: File })
        const fileNameHash = createHash('sha1').update(file.name).digest('base64')
        const fileRef = storageRef.child(`${auth?.currentUser?.uid}/${new Date().getTime()}_${fileNameHash}`)
        const { ref } = await fileRef.put(file)
        const downloadURL = await ref.getDownloadURL()
        console.log({ downloadURL })
        target.value = null
        await tryContentCardUpload({
          type: 'video',
          sourceUrl: downloadURL,
          previewVideo: downloadURL,
          rawVideos: [downloadURL],
          ts: new Date(),
          title: '',
          description: ''
        })
       } catch ({ code, message }) {
        console.error('handleVideoFile Error', { code, message })
      }
      reactiveFields.isUploading = false
    }

    const handleSocialLinkClick = (site: string, currentLink = '') => {
      reactiveFields.editSocialLinkMode = !reactiveFields.editSocialLinkMode
      if (!reactiveFields.editSocialLinkMode) {
        reactiveFields.socialLinkInputFor = ''
        reactiveFields.trySocialLink = ''
        return
      }
      reactiveFields.socialLinkInputFor = site
      reactiveFields.trySocialLink = currentLink
    }

    const updateSocialLink = (site: string, link: string) => {
      updateProfileValue(`socialLinks.${site}.link`, link)
    }
 
    return {
      ...toRefs(reactiveFields),
      isOwnProfile,
      isLoggedIn,
      signOut,
      tryContentCardUpload,
      handleImageFile,
      handleVideoFile,
      invertedHex,
      handleSocialLinkClick,
      updateSocialLink,
      socialLinksToShow,
      shareLinks
    }
  },
}
