<template>
    <div class="m-2 text-right">
      
      <button v-if="isLoggedIn" type="button" class="btn btn-light border m-1" @click="signOut">Close Session</button>
      <!-- button v-if="isOwnProfile && !editMode" type="button" class="btn btn-light border m-1" @click="editColorMode = !editColorMode">{{editColorMode ? 'Hide Color-Picker' : 'Color-Picker'}}</button -->
      <!-- button type="button" class="btn btn-light border m-1" @click="showShareProfile = !showShareProfile">{{showShareProfile ? 'Hide Bio Share' : 'Bio Share'}}</button -->
      
    </div>

